import React from 'react'
import { 
  ListElement,
  PreviewImageContainer,
  InfoGroup,
  Header,
  SizeList,
  PriceTag,
  UpdateDate
} from './ShoesListElement.style'

const ShoesListElement = ({ model, getType, filter, isSizeFilterSet }) => (
  <ListElement key={model.id} data-type={getType(model.name)}>
    <PreviewImageContainer href={model.url}> 
      <img src={model.img} alt="preview" />
    </PreviewImageContainer>
    <InfoGroup>
      <Header> <a href={model.url}> {model.name} </a> </Header>
      <SizeList>
        {model.sizes
          .filter(size => isSizeFilterSet() ? filter.size.includes(size) : true)
          .map(size => 
          <li key={size}> {size} </li>
        )}
      </SizeList>
      <PriceTag> {model.price} zł </PriceTag>
      <UpdateDate> {model.lastUpdated} </UpdateDate>
    </InfoGroup>
  </ListElement>
)

export default ShoesListElement