import { Switch, Route } from 'react-router-dom'
import { GlobalStyles } from './GlobalStyles.style'
import { AppContainer } from './App.style'

import Nav from './components/Nav'
import ShoesList from './components/ShoesList'
import Filters from './components/Filters'

const App = () => {
  return (
    <AppContainer>
      <GlobalStyles />
      <Nav />

      <Switch>
        <Route path="/about">
          <div> Filters are being saved in your cookies everytime you make a change </div>
          <div> The data is being refreshed every day at 00:00 </div>
        </Route>
        <Route path="/">
          <Filters />
          <ShoesList />
        </Route>
      </Switch>
    </AppContainer>
  );
}

export default App;
