import React from 'react'
import { Link } from 'react-router-dom'
import { NavContainer, Brand, NavLinks, NavLink } from './Nav.style'

const Nav = () => {
  return (
    <NavContainer>
      <Brand>
        <Link to="/">
          VANS-Superior
        </Link>
      </Brand>
      <NavLinks>
        <NavLink>
          <Link to="/">
            Models
          </Link>
        </NavLink>
        <NavLink>
          <Link to="/about">
            About
          </Link>
        </NavLink>
      </NavLinks>
    </NavContainer>
  )
}

export default Nav